import React, { Component, Fragment } from "react";
import { connect } from "react-redux";
import { Route, Switch } from "react-router-dom";
import { ConnectedRouter as Router } from "connected-react-router";
import { history } from "../redux";
import { ToastContainer } from "react-toastify";

import { path } from "../utils";

import Home from "../routes/Home";
// import Login from '../routes/Login';

import Header from "./Header/Header";
import HomeFooter from "./HomePage/HomeFooter";
import { CustomToastCloseButton } from "../components/CustomToast";
import ConfirmModal from "../components/ConfirmModal";
import HomePage from "./HomePage/HomePage.js";
import AboutUs from "./HomePage/aboutUs";
import Payment from "./HomePage/payment";
import News from "./HomePage/news";
import Feedback from "./HomePage/feedback";
import Contact from "./HomePage/contact";
import ZaloContact from "./HomePage/zaloContact";
import CustomScrollbars from "../components/CustomScrollbars";
class App extends Component {
  handlePersistorState = () => {
    const { persistor } = this.props;
    let { bootstrapped } = persistor.getState();
    if (bootstrapped) {
      if (this.props.onBeforeLift) {
        Promise.resolve(this.props.onBeforeLift())
          .then(() => this.setState({ bootstrapped: true }))
          .catch(() => this.setState({ bootstrapped: true }));
      } else {
        this.setState({ bootstrapped: true });
      }
    }
  };

  componentDidMount() {
    this.handlePersistorState();
  }

  render() {
    return (
      <Fragment>
        <Router history={history}>
          <div className="main-container">
            <Header />
            <div className="content-container">
              <CustomScrollbars style={{ height: "91vh", width: "100%" }}>
                <Switch>
                  <Route path={path.HOME} exact component={Home} />
                  <Route path={path.HOMEPAGE} component={HomePage} />
                  <Route path={path.ABOUTUS} component={AboutUs} />
                  <Route path={path.PAY} component={Payment} />
                  <Route path={path.NEWS} component={News} />
                  <Route path={path.FEEDBACK} component={Feedback} />
                  <Route path={path.CONTACT} component={Contact} />
                </Switch>
                <HomeFooter />
                <ZaloContact />
              </CustomScrollbars>
            </div>

            <ToastContainer
              className="toast-container"
              toastClassName="toast-item"
              bodyClassName="toast-item-body"
              autoClose={false}
              hideProgressBar={true}
              pauseOnHover={false}
              pauseOnFocusLoss={true}
              closeOnClick={false}
              draggable={false}
              closeButton={<CustomToastCloseButton />}
            />
          </div>
        </Router>
      </Fragment>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    started: state.app.started,
    isLoggedIn: state.user.isLoggedIn,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
